.movie {
    display: inline-block;
    padding: 30px;
    border-radius: 10px;
    background-color: #fff;
    box-shadow: 8px 8px 16px hsl(0deg 0% 0% / 0.1);
}

.coverImg {
    float: left;
    width: 10vw;
    margin-bottom: 50px;
}

.info {
    display: inline-block;
    width: 300px;
    margin-left: 30px;
}

.title {
    font-size: 2.2rem;
    font-weight: 400;
}

.year {
    margin: 5px 0 20px;
    font-size: 1.6rem;
    font-weight: 300;
}

.genres {
    float: left;
    margin: 0 10px 20px 0;
    font-size: 1.8rem;
}

.summary {
    display: -webkit-box;
    width: 100%;
    font-size: 1.4rem;
    overflow: hidden;
    text-overflow: ellipsis;
    word-break: keep-all;
    -webkit-line-clamp: 7;
    -webkit-box-orient: vertical;
}