@charset "UTF-8";

.detail {
    color: #fff;
}

.gnb {
    display: flex;
    align-items: center;
    width: 100%;
    height: 50px;
    padding: 0 20px;
    background-color: #000;
    box-shadow: 8px 8px 16px hsl(0deg 0% 0% / 0.1);
}

.gnb ul li {
    font-size: 2rem;
}

.gnb ul li a {
    color: #fff;
}

.coverBg {
    width: 100%;
    opacity: 0.95;
}

.movie {
    position: absolute;
    top: 50%;
    left: 300px;
    transform: translateY(-50%);
    width: 80vw;
}

.coverImg {
    float: left;
    border: 5px solid #fff;
}

.info {
    float: left;
    margin-left: 50px;
}

.title {
    font-size: 2.4rem;
    margin-bottom: 10px;
}

.info p {
    margin-top: 30px;
    font-size: 2rem;
}

.info p:nth-of-type(1) {
    margin: 10px 0 50px;
    font-size: 2rem;
}

.info p:nth-of-type(3) {
    font-size: 1.8rem;
}