@charset "UTF-8";
@import url(//spoqa.github.io/spoqa-han-sans/css/SpoqaHanSansNeo.css);

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    font-size: 0.625em;
}

body {
    font-family: 'Spoqa Han Sans Neo', 'sans-serif';
}

li {
    list-style: none;
}

a {
    text-decoration: none;
    color: #000;
}

.main {
    background-color: rgb(207, 226, 255);
}

.gnb {
    display: flex;
    align-items: center;
    width: 100%;
    height: 50px;
    margin-bottom: 80px;
    padding: 0 20px;
    background-color:  rgb(207, 226, 255);
    box-shadow: 8px 8px 16px hsl(0deg 0% 0% / 0.1);
}

.gnb ul li {
    font-size: 2rem;
}

.movies {
    display: grid;
    grid-template-columns: auto auto;
    gap: 80px;
    width: 80vw;
    margin: 0 auto;
}